body,
p {
  margin: 0;
  padding: 0;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 320px;
  height: 100vh;
}

.guide-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.inp_file {
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 1px;
}

.text-center {
  text-align: center;
}

.plus-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  margin-top: 80px;
  border: 1px solid #707070;
  font-size: 24px;
  line-height: 24px;
}

.image-group {
  display: flex;
  flex-wrap: wrap;
  overflow-y: auto;
  max-height: 70vh;
  max-width: 850px;
  margin-top: 30px;
  margin-left: -10px;
  padding: 0 10px;
}

.image-box {
  overflow: hidden;
  width: 200px;
  height: 200px;
  margin: 10px 0 0 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-sizing: border-box;
}

.image-box img {
  display: block;
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 768px) {
  .plus-box {
    margin-top: 40px;
  }

  .image-group {
    max-width: none;
    max-height: 55vh;
    margin-left: 0;
    padding-right: 10px;
  }

  .image-box {
    flex-basis: calc(50% - 20px);
    position: relative;
    height: auto;
    padding-top: 35%;
  }

  .image-box img {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    bottom: 0;
    height: auto;
    transform: translateY(-50%);
  }
}
